import { Observable, of } from 'rxjs';
import { HasId } from './has-id';
import { ActionResult } from './actuator/action-result';
import { Style } from '../ui-concepts/style';
import { IActuator } from './actuator/i-actuator';
import { IValidator } from './validator/i-validator';

export class ActionItem<T extends HasId> {
  name: string;
  longName?: string;
  icon?: string;
  style?: Style;
  actuator: IActuator<T>;
  actionValidator?: IValidator<T>;
  enabled?: boolean;

  constructor(object: {
    name: string;
    longName?: string;
    icon?: string;
    icon_stacked?: string;
    style?: Style;
    actuator?: IActuator<T>;
    enabled?: boolean;
    fullButton?: boolean;
    actionValidator?: IValidator<T>;
  }) {
    this.name = object.name;
    this.longName = object.longName ? object.longName : object.name;
    this.icon = object.icon;
    this.style = object.style ? object.style : Style.NORMAL;
    this.actuator = object.actuator;
    this.actionValidator = object.actionValidator;
    this.enabled = object.enabled;
  }

  initializeItem() {
    const self = this;
    this.icon = this.icon && this.icon !== '' ? this.icon : 'fa fa-cogs';
    this.enabled = true;
    this.actionValidator = this.actionValidator
      ? this.actionValidator
      : new class implements IValidator<T> {
        isActionValid = (): boolean => true;
      }();
    this.actuator = this.actuator
      ? this.actuator
      : new class implements IActuator<T> {
        action(elements: T[]): Observable<ActionResult> {
          // tslint:disable-next-line:no-console
          console.debug(self.name + ' clicked on elements ' + elements.map(value => value ? value.id : '' + '').join(','));
          return of({});
        }
      }();
  }
}
